<template>
  <div class="cardVoucher mescroll">
    <div class="top">
      <div class="flex">
        <div class="left">
          <p class="label">我的兑换券</p>
          <p class="value">{{curr_balance.toFixed(2)}}</p>
        </div>
        <div class="right">
          <div
            class="btn"
            @click="$router.push({name:'exchangeMallIndex'})"
          >去兑换</div>
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="mid-top">
        <div class="flex">
          <p
            class="left"
            @click="showDate = true"
          >{{date}}</p>
          <img
            class="icon"
            src="../../assets/img/icon/down.png"
            alt
            @click="showDate = true"
          />
        </div>
        <div class="flex">
          <p class="flex-2">
            收入
            <span style="color:#eb5252">¥{{total_increase_money.toFixed(2)}}</span>
          </p>
          <p class="flex-2">
            支出
            <span style="color:#000">¥{{total_decrease_money.toFixed(2)}}</span>
          </p>
          <!-- <div class="flex-1 right">
            <p>| 月账单</p>
            <img src="../../assets/img/icon/left.png" alt>
          </div>-->
        </div>
      </div>
      <div class="list">
        <div
          class="item"
          v-for="(item,index) in list"
          :key="index"
        >
          <div class="left">
            <p class="title">{{item.trade_describe}}</p>
            <p class="time">{{item.trade_time.substring(0,10)}}</p>
          </div>
          <div class="right">
            <p
              class="price"
              :style="item.trade_type == '2'?'color:#333':''"
            >{{item.trade_type == '2'?('-' + item.decrease_money.toFixed(2)):('+' + item.increase_money.toFixed(2))}}</p>
          </div>
        </div>
      </div>
    </div>
    <van-popup
      v-model="showDate"
      position="bottom"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        :formatter="formatter"
        @confirm="dateConfirm"
        @cancel="showDate = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Popup, DatetimePicker } from "vant";
// import { getBalanceDetails } from "@/api";
export default {
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker
  },
  data() {
    return {
      showDate: false,
      currentDate: new Date(),
      date: "",
      curr_balance: 0,
      total_increase_money: 0,
      total_decrease_money: 0,
      list: []
    };
  },
  methods: {
    formatter(type, value) {
      if (type === "year") {
        return `${value}年`;
      } else if (type === "month") {
        return `${value}月`;
      }
      return value;
    },
    dateConfirm(value) {
      let year = new Date(value).getFullYear(),
        month = new Date(value).getMonth() + 1,
        day = new Date(value).getDate(),
        hour = new Date(value).getHours(),
        min = new Date(value).getMinutes();
      this.date = `${year}年${month < 10 ? "0" + month : month}月`;
      this.showDate = false;
      this.loadData();
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getBalanceDetails({
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        type: "2",
        date: this.date.replace("年", "-").replace("月", "")
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.curr_balance = res.data.curr_balance;
          this.total_decrease_money = res.data.fund_total.total_decrease_money;
          this.total_increase_money = res.data.fund_total.total_increase_money;
          this.list = res.data.fund_list;
        }
      });
    }
  },
  mounted() {
    document.title = "优惠券";
    this.date = `${new Date().getFullYear()}年${
      new Date().getMonth() + 1 < 10
        ? "0" + (new Date().getMonth() + 1)
        : new Date().getMonth() + 1
    }月`;
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.cardVoucher {
  .top {
    background: $base-color;
    margin: 15px;
    border-radius: 5px;
    padding: 20px 0;
    .flex {
      display: flex;
      padding: 15px;
      color: #fff;
      align-items: center;
      .left {
        flex: 1;
        .label {
          font-size: 13px;
          line-height: 40px;
        }
        .value {
          font-size: 30px;
          line-height: 40px;
        }
      }
      .right {
        flex: 1;
        .btn {
          width: 80px;
          height: 35px;
          line-height: 35px;
          border-radius: 5px;
          background: #fff;
          color: #eb5252;
          float: right;
          text-align: center;
          font-size: 13px;
          font-weight: bold;
        }
      }
    }
  }
  .mid {
    margin-top: 25px;
    &-top {
      border-bottom: 1pt solid #e1e1e1;
      padding-bottom: 20px;
      .flex {
        display: flex;
        padding: 5px 15px;
        align-items: center;
        .left {
          font-size: 18px;
          font-weight: bold;
        }
        .icon {
          width: 13px;
          margin-left: 5px;
        }
        .flex-2 {
          flex: 2;
          color: #666;
        }
        .right {
          width: 60px;
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #999;
          img {
            width: 5px;
            margin-left: 5px;
          }
        }
      }
    }
    .list {
      padding: 0 15px;
      .item {
        padding: 10px 0;
        display: flex;
        align-items: center;
        border-bottom: 1pt solid #e1e1e1;
        .left {
          flex: 2;
          line-height: 25px;
          .title {
            font-size: 15px;
          }
          .time {
            color: #999;
            font-size: 13px;
          }
        }
        .right {
          text-align: right;
          .price {
            font-size: 18px;
            color: $base-color;
          }
        }
      }
    }
  }
}
</style>
